<template>
  <div id="app">
    <nav :style="{ backgroundColor: navBackground }">
      <router-link to="/"></router-link>
      <router-link to="/ranking"></router-link>
      <router-link to="/classify"></router-link>
    </nav>
    <router-view />
  </div>
</template>
<script>
export default {
  data() {
    return {
      navBackground: 'transparent', // 
    };
  },
  methods: {
    handleScroll() {
      const scrollY = window.scrollY;
      const opacity = Math.min(scrollY / 200, 1);
      this.navBackground = `rgba(255, 255, 255, ${opacity})`; // 
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll); // 
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll); // 
  },
};
</script>

