#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;

  nav {
    background-color: aliceblue; // 
    transition: opacity .3s; // 

    width: 66vw;
    position: fixed;
    background: transparent;
    top: 0;
    left: 0;
    height: 20rem;
    padding: 15rem 0;

    a {
      font-weight: bold;
      color: #2c3e50;

