import axios from "./axios.js";

const getIndexIndex = () => axios.get("/index/index"); //

const getndexExchangeI = () => axios.get("/index/exchange"); //

const getIndexMore = () => axios.get("/index/more"); //

const getTopIndex = (id) => axios.get(`/top/index?id=${id}`); //

const getTopList = (data = {}) => axios.get('/top/list', { params: data }); //

const getHaokanCategory = () => axios.get("/category/list"); //
const getHaokanCategoryBooks = (options) =>
    axios.get("/book/list", {
        params: options,
    }); 
    
const getSoHot = () => axios.get("/so/hot"); //

// https://apis.netstart.cn/haokan/so/comic?keyword=&p=1
const getSoComic = (options) => axios.get("/so/comic", {
    params: options,
});         

const getComicDetail = (id) => axios.get(`/book/show?id=${id}`); //

const getExchangeData = (id) => axios.get(`/index/exchange?id=${id}`); // 

const getBookShowChapter = (id) => axios.get(`/book/showChapter?id=${id}`);    //

const getBookListChapter = (id) => axios.get(`/book/listChapter?id=${id}`); //

const getDayList = (id) => axios.get(`/day/list?id=${id}`);  //

const getCommentListHot= (did) => axios.get(`/comment/listHot?did=${did}`); //

const getBookRecommend = (comicId) => axios.get(`/book/recommend?id=${comicId}`); //

export {
    getBookListChapter, getBookShowChapter, getIndexIndex, getndexExchangeI, getTopIndex,
    getTopList, getHaokanCategory, getHaokanCategoryBooks, getSoHot, getSoComic, getComicDetail,
    getExchangeData ,getDayList, getCommentListHot ,getBookRecommend, getIndexMore
};
