// src/store/index.js
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('token') || null, //  localStorage  token
    chapterIds: [],
    favorites: JSON.parse(localStorage.getItem('favorites')) || [],
    isLoggedIn: !!localStorage.getItem('token'), //  token 
  },
  getters: {

  },
  mutations: {
    addFavorite(state, comicId) {
      if (!state.favorites.includes(comicId)) {
        state.favorites.push(comicId);
        localStorage.setItem('favorites', JSON.stringify(state.favorites));

      }
    },
    removeFavorite(state,comicId){
      const index=state.favorites.indexOf(comicId);
      if(!index>-1){
        state.favorites.splice(index,1);
        localStorage.setItem('favorites',JSON.stringify(state.favorites));
      }
    },
    setToken(state, token) {
      state.token = token;
      state.isLoggedIn = !!token;
      if (token) {
        localStorage.setItem('token', token); //  token
      } else {
        localStorage.removeItem('token'); //  token
      }
    },
    logout(state) {
      state.token = null;
      state.isLoggedIn = false;
    },

  },
  actions: {
    toggleFavorite( { commit ,  state }, comicId){
      if (state.favorites.includes(comicId)) {
        commit('removeFavorite', comicId); // 
      } else {
        commit('addFavorite', comicId); // 
      }
    },
    login({ commit }, token) {
      commit('setToken', token); //  token
    },
    logout({ commit }) {
      commit('setToken', null); //  token
    },
  },
  modules: {},
});